export default {
	props: {
		hideout: {
			default: () => {},
			type: Object
		}
	},
	methods: {
		getHideout (hash) {
			return this.$store.getters.getHideout(hash)['Name'];
		},
		getImage (hideout) {
			if (/.jpg|.png|jpeg/gi.test(hideout.hideoutScreenshot)) {
				return hideout.hideoutScreenshot;
			} else {
				const image = this.$store.getters.getHideout(hideout.hideoutType)['Icon'];
				return image;
			}
		}
	}
};
